@import url(https://fonts.googleapis.com/css?family=Comfortaa|Karla);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #6ea0ff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Comfortaa', cursive;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.tabs {
	color: white;
	font-family: 'Comfortaa';
	position:fixed;
	right: 0px;
}

.toolbar {
	background-color: #6ea0ff;
  	font-family: 'Comfortaa', sans-serif;
  	justify-content: right;
}

h1 {
	color: black;
	font-family: 'Comfortaa';
	font-size: 36px;
	color: black;
}

h2 {
	color: black;
  	font-family: 'Karla', sans-serif;
  	font-size: 24px;
  	font-weight: bold;
  	text-align: left;
}

h3 {
	color: #666666;
  	font-family: 'Karla', sans-serif;
  	font-size: 18px;
  	text-align: left;
}

h4 {
	color: black;
	font-family: 'Karla';
	font-size: 12px;
	font-weight: bold;
}

p {
	font-family: 'Karla';
	font-size: 12px;
	color: #666666;
}

.bigGrid {
	position: absolute;
	top: 100px;
	left: 20px;
}


.mainLogo {
	width: 220px;
	height: auto;
}

.inputBar {
	content: text;
	width: 100%;
	min-width: 50px;
	height: 30px;
	font-size: 18px;
	border: 2px;
	border-color: #adb7ca;
	border-style: solid;
	border-radius: 5px;
	padding-left: 10px;
	left: 50px;
}

::-webkit-input-placeholder {
	color: #adb7ca;
}

:-ms-input-placeholder {
	color: #adb7ca;
}

::-ms-input-placeholder {
	color: #adb7ca;
}

::placeholder {
	color: #adb7ca;
}

.searchButton {
	height: 36px;
	border-radius: 5px;
	font-size: 20px;
	border-color: #adb7ca;
	color: #adb7ca;
	border:2px;
	border-style: solid;

}

.searchButton:hover {
	cursor: pointer;
}

.filterCategory {
}

.logo {
	width: 40px;
	height: 35px;
}

.searchGrid {
	align-items: "center";
	justify: "flex-start";
}

.filterCategories2 {
	position: relative;
	top: 10px;

}

.dateChip:hover {
	cursor: pointer;
}

.dateGrid {
	width: 100px;
}

.filler {
	width: 100px;
}

.shortDescription {
	text-align: left;
	font-size: 16px;
}

.eventTitle {
	color: black;
}

.eventDate {
	color: black;
}

@media all and (min-width: 480px) {
  .Login {
    padding: 60px 0;
  }

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
}

